body {
  margin: 0;
  font-family: "Nanum Gothic", sans-serif;
  min-width: 500px;
  background-color: #f4f2f4;
  color: #f4f2f4;
}
.navbar {
  width: 100%;
  height: 50px;
  top: 0;
  margin-bottom: 30px;
  background-color: #947d94;
  color: #f4f2f4;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}
h4 {
  margin: auto 20px;
  font-size: 22px;
}
.section {
  height: 85vh;
  min-height: 400px;
  border: 2px solid #ded8de;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}
/* left part */
.left {
  width: 600px;
  background-image: url("body/body.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.part {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 90px;
  flex: 1;
}
.part-top,
.part-bottom {
  flex: 2;
}

.eyes {
  flex: 2;
}
.eyes img {
  max-width: 400px;
  height: 100%;
  object-fit: contain;
}
.nose {
  height: 100%;
  object-fit: contain;
}
.middle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 80%;
}
.ear {
  height: 100%;
  width: 50px;
}
.left-ear {
  transform: scale(-1, 1);
}
.mouth {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}
.mouth img {
  max-width: 350px;
  height: 100%;
  object-fit: contain;
}

/* right part */
.right {
  width: 600px;
  overflow-y: hidden;
}
.top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 50px;
  border-bottom: 2px solid #ded8de;
}
.selected {
  background-color: #947d94;
}
.option {
  flex: 1;
  border-right: 1px solid #ded8de;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #947d94be;
  cursor: pointer;
}
.option:hover {
  background-color: #947d94;
}
.center {
  overflow-y: scroll;
  height: 78vh;
  min-height: 400px;
  display: flex;
  align-items: center;
  flex-direction: column;
  /* height: 100%; */
}
.main-section {
  display: grid;
  width: 90%;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin: 10px 0;
}
.element {
  border: 2px solid #ded8dea1;
  /* width: 100px;
  height: 100px; */
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.element:hover {
  background-color: whitesmoke;
}

.body-part_img {
  width: 100%;
  max-width: 140px;
  max-height: 180px;
  object-fit: contain;
}
.element img {
  object-fit: contain;
}

/* scroll */

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: whitesmoke;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ded8de;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #947d94;
}

@media screen and (max-width: 912px) {
  .main-section {
    grid-template-columns: 1fr;
  }
  .left {
    width: 60vw;
  }
  .right {
    width: 35vw;
  }
  img {
    transform: scale(0.8);
  }
  .left-ear {
    transform: scale(-1, 0.8);
  }
  .part-container {
    width: 70%;
    justify-self: center;
  }
}
@media screen and (max-width: 600px) {
  .container {
    flex-direction: column;
  }
  .left {
    height: 65vh;
    width: 100vw;
  }
  .right {
    width: 100%;
    height: max-content;
  }
  .center {
    height: max-content;
    min-height: auto;
  }
  .main-section {
    grid-template-rows: 1fr;
    grid-template-columns: repeat(12, 1fr);
    overflow-x: scroll;
  }
  .element {
    width: 200px;
    height: 300px;
  }
}
